import React from 'react';
import styled from 'styled-components';

const Icon = (props) => {
  const a = props.icon;
  const SVG = styled.svg`
    ${a}
    ${props => console.log(props) || props.styled};
  `;
  return (
    <SVG viewBox='0 0 16 16' className={`icon icon-${props.icon}`}>
      <use xlinkHref={`../static/icons/sprite.svg#${props.icon}`} />
    </SVG>
  )
}

export default Icon;